import React from 'react';
import { Router, Route, Switch } from 'react-router-dom';
import { createBrowserHistory } from 'history';
import Categories from './components/categoriesComponent';
import Category from './components/categoriesComponent/category';
import Cocktail from './components/cocktailComponent';
import Home from './components/homeComponent';
import Glasses from './components/glassesComponent';
import Glass from './components/glassesComponent/glass';
import Ingredients from './components/ingredientsComponent';
import Ingredient from './components/ingredientsComponent/ingredient';
import AlchoholicFilters from './components/alchoholicFilterComponent';
import AlchoholicFilter from './components/alchoholicFilterComponent/alchoholicFilter';
import CocktailSearch from './components/searchComponent';

const hist = createBrowserHistory();

const AppRouter = () => {
  return (
    <div>
      <Router history={hist}>
        <Switch>
          <Route path='/Jyoti-Cocktail-Bar/categories' exact component={Categories} />
          <Route path='/Jyoti-Cocktail-Bar/category' exact component={Category} />
          <Route path='/Jyoti-Cocktail-Bar/glasses' exact component={Glasses} />
          <Route path='/Jyoti-Cocktail-Bar/glass' exact component={Glass} />
          <Route path='/Jyoti-Cocktail-Bar/ingredients' exact component={Ingredients} />
          <Route path='/Jyoti-Cocktail-Bar/ingredient' exact component={Ingredient} />
          <Route path='/Jyoti-Cocktail-Bar/alcoholic-filters' exact component={AlchoholicFilters} />
          <Route path='/Jyoti-Cocktail-Bar/alcoholic-filter' exact component={AlchoholicFilter} />
          <Route path='/Jyoti-Cocktail-Bar/cocktail/:id' exact component={Cocktail} />
          <Route path='/Jyoti-Cocktail-Bar/search' exact component={CocktailSearch} />
          <Route path='/Jyoti-Cocktail-Bar/' exact component={Home} />
        </Switch>
      </Router>
    </div>
  );
};

export default AppRouter;